import { useState, useEffect } from "react";
import { ChatInterface } from "types/chat";
import { getDatabase, ref, onValue, push } from "firebase/database";

import css from "./chat.module.scss";

const Chat = () => {
  // States
  const [data, setData] = useState({
    name: "",
    message: "",
  });
  const [chats, setChats] = useState<any[]>([]);

  // Variables
  const db = getDatabase();
  const comments = ref(db, "comments/");

  const getChats = () => {
    onValue(comments, (snapshot) => {
      const data = snapshot.val();
      // eslint-disable-next-line eqeqeq
      if (typeof data === "object" && (data != null || data != undefined)) {
        const result = [];
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        for (let [_, value] of Object.entries(data)) {
          result.push(value);
        }
        setChats(result.reverse());
      }
    });
  };

  // Handlers
  const handleChange = (event: any) => {
    const { value, name } = event.target;
    setData((state) => ({ ...state, [name]: value }));
  };

  const handleClick = async () => {
    const { name, message } = data;
    push(ref(db, "comments/"), { name, message, date: Date.now() });
    setData({ name: "", message: "" });
  };

  const handleDate = (value: string | Date) => {
    let result = value;

    const currentDate = new Date(value);
    const date = currentDate.getDate();
    const month = currentDate.getMonth() + 1;
    const hour = currentDate.getHours();
    const minute = currentDate.getMinutes();
    result = `${date}/${month} ${hour}:${minute}`;

    return result;
  };

  // Hooks
  useEffect(() => {
    getChats();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div id="Chat" className={css.wrapper}>
      <div className={css.form}>
        <h1>Tulis ucapan</h1>
        <div>
          <input
            type="text"
            name="name"
            onChange={handleChange}
            placeholder="Nama"
            value={data?.name}
          />
        </div>
        <div>
          <textarea
            onChange={handleChange}
            name="message"
            placeholder="Tulis pesan Anda di sini"
            rows={5}
            value={data?.message}
          ></textarea>
        </div>
        <button
          type="button"
          disabled={!data?.name || !data?.message}
          onClick={handleClick}
        >
          Submit
        </button>
        <h1>Terimakasih</h1>
      </div>
      <div className={css.list}>
        <ul>
          {chats.length
            ? chats.map((item: ChatInterface, index: number) => (
                <li key={index.toString()}>
                  <div className={css.chat}>
                    <div className={css.chat_header}>
                      <strong>{item?.name}</strong>
                      <span>{handleDate(item?.date)}</span>
                    </div>
                    <div className={css.chat_body}>{item?.message}</div>
                  </div>
                </li>
              ))
            : null}
        </ul>
      </div>
    </div>
  );
};

export default Chat;
